<template lang="html">
  <div class="body">
    <Affix :offset-top="300" class="right-button">
      <ButtonGroup vertical>
          <Button type="ghost" icon="social-whatsapp" @click="instance('phone')"></Button>
          <Button type="ghost" icon="social-facebook" @click="instance('face')"></Button>
          <Button type="ghost" icon="email" @click="instance('email')"></Button>
          <Button type="ghost" icon="social-github" @click="instance('github')"></Button>
      </ButtonGroup>
    </Affix>
    <Row class="item-list" >
        <Col class="item-box" :xs="24" :sm="12" :md="8" :lg="8" v-for="(ele,indexP) in pro" :key='ele.title'>
          <h3>{{ele.title}}</h3>
          <ul class="web-work clearfix" >
            <li v-for="(item,indexC) in ele.list" :key="item.href">
              <router-link :to="{ name: item.route}" v-show="!item.href">
              <!-- <router-link :to="item.route" v-show="!item.href"> -->
                <img :src='item.img'>
                <div class="shadow" :class="{'show': item.noShow}">
                  <div class="discript">
                    <h2>{{item.intru.title}}</h2>
                    <p class="clearfix"><span></span><span v-html="item.intru.content[0]"></span></p>
                    <p class="clearfix"><span></span><span v-html="item.intru.content[1]"></span></p>
                  </div>
                </div>
                <span class="small-tip" @mouseenter="toggleShow(indexP,indexC)" @mouseleave="toggleShow(indexP,indexC)">
                  <i></i><i></i><i></i>
                </span>
              </router-link>
              <a :href='item.href' v-show="item.href">
                <img :src='item.img'>
                <div class="shadow" :class="{'show': item.noShow}">
                  <div class="discript">
                    <h2>{{item.intru.title}}</h2>
                    <p class="clearfix"><span></span><span v-html="item.intru.content[0]"></span></p>
                    <p class="clearfix"><span></span><span v-html="item.intru.content[1]"></span></p>
                  </div>
                </div>
                <span class="small-tip" @mouseenter="toggleShow(indexP,indexC)" @mouseleave="toggleShow(indexP,indexC)">
                  <i></i><i></i><i></i>
                </span>
              </a>
            </li>
          </ul>
        </Col>
    </Row>
  </div>
</template>

<script>
export default {
  components: {},
  data () {
    return {
      pro: [
        {
          title: '',
          list: [
            {
              img: require('@/assets/img/work-back.png'),
              href: 'http://adadm.calli-media.com',
              // route: 'Work1',
              intru: {
                title: 'iptv',
                content: [
                  'bootstrap, echarts,jquery,arttemplate',
                  '1.bootstrap</br> 2.</br> 3.</br> 4.echarts'
                ]
              }
            },
            {
              img: require('@/assets/img/xcc.png'),
              route: 'Work2',
              intru: {
                title: '',
                content: [
                  ', websocket',
                  '1.</br> 2.DOM</br> 3.</br> 4. </br> 5.apitabbarvideo'
                ]
              }
            },
            {
              img: require('@/assets/img/www.png'),
              href: 'http://www.calli-media.com',
              intru: {
                title: '',
                content: [
                  'fullpage.js, jquery, wow.js, animate.css',
                  '1.</br> 2.PC</br> 3.'
                ]
              }
            }
          ]
        },
        {
          title: '',
          list: [
            {
              img: require('@/assets/img/auto-change.png'),
              href: 'https://hanfengmi.github.io/auto-change',
              intru: {
                title: '',
                content: [
                  'css3, @media',
                  '1.html</br> 2.css3</br> 3.media</br>'
                ]
              }
            },
            {
              img: require('@/assets/img/my-web.png'),
              href: 'https://hanfengmi.github.io/my-web',
              intru: {
                title: '',
                content: [
                  'html, js, css3, svg, js, js',
                  '1.html</br> 2.hashchangehash</br> 3.3D</br>'
                ]
              }
            },
            {
              img: require('@/assets/img/monilvzuan.png'),
              href: 'https://hanfengmi.github.io/monilvzuan',
              intru: {
                title: '',
                content: [
                  'html',
                  '1.htmlW3C</br> 2.floatposition'
                ]
              }
            },
            {
              img: require('@/assets/img/phone.png'),
              href: 'https://hanfengmi.github.io/phone',
              intru: {
                title: 'app',
                content: [
                  'html, js',
                  '1.htmlW3C</br> 2.jsrem</br>'
                ]
              }
            }
          ]
        },
        {
          title: '',
          list: [
            {
              img: require('@/assets/img/dh1.gif'),
              route: 'Texiao1',
              intru: {
                title: '',
                content: [
                  '',
                  ''
                ]
              }
            },
            {
              img: require('@/assets/img/dh2.gif'),
              route: 'Texiao2',
              intru: {
                title: '',
                content: [
                  '3D',
                  '3D'
                ]
              }
            },
            {
              img: require('@/assets/img/dh3.gif'),
              route: 'Texiao3',
              intru: {
                title: 'css3',
                content: [
                  'css3+svg',
                  '...'
                ]
              }
            }
          ]
        }
      ]}
  },
  props: ['personsIn'],
  watch: {},
  filters: {},
  computed: {},
  created () {},
  mounted () {},
  destroyed () {},
  methods: {
    instance (type) {
      var that = this
      switch (type) {
        case 'phone':
          this.$Modal.info({
            title: '',
            content: this.personsIn.tel + '<p>call</p>'
          })
          break
        case 'email':
          this.$Modal.info({
            title: '',
            content: this.personsIn.email + '<p></p>'
          })
          break
        case 'face':
          this.$Modal.error({
            title: '',
            content: this.personsIn.face
          })
          break
        case 'github':
          this.$Modal.warning({
            title: '',
            content: 'github'
          })

          setTimeout(function () {
            window.location.href = that.personsIn.github
          }, 1000)
          break
      }
    },
    toggleShow (indexP, indexC) {
      if (this.pro[indexP].list[indexC].noShow) {
        this.$set(this.pro[indexP].list[indexC], 'noShow', false)
      } else {
        this.$set(this.pro[indexP].list[indexC], 'noShow', true)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@keyframes big {
  0% {
    transform: scale(0.5);
    opacity: 1;
  }
  100% {
    transform: scale(2.5);
    opacity: 0;
  }
}
.body {
  // margin-top:520px;
  background: #fff;
  position:relative;
  color:#000;
  margin:500px auto 0 auto;
}
.clearfix::after {
  content: '';
  display: block;
  clear:both;
}
.right-button {
  position: absolute;
  top:60px;
  left:0px;
}
.item-list {
  width:80%;
  height:100%;
  margin:0 auto;
  padding-top:20px;
  h3 {
    height:30px;
    font-weight: 400;
    line-height: 30px;
  }
  .item-box {
    ul {
      li {
        width:100%;
        box-sizing: border-box;
        padding: 5px;
        padding-bottom:20px;
        margin-bottom:20px;
        border-bottom:3px solid #c7c7c7;
        float: left;
        position: relative;
        .show {
          display: block !important;
        }
        .shadow {
          position:absolute;
          width:100%;
          height:100%;
          top:0;
          left:0;
          background: rgba(0,0,0,.5);
          display: none;
          .discript {
            width:100%;
            height:400px;
            background: rgba(250,250,250,.9);
            position:absolute;
            box-sizing: border-box;
            top:50%;
            padding: 60px 20px;
            margin-top: -200px;
            border-radius: 20px;
            h2 {
              margin:20px 0;
              color: #666;
            }
            p {
              text-align: left;
              font-size:14px;
              margin-top:10px;
              span {
                float:left;
                height:100%;
              }
              span:nth-of-type(1){
                width:30%;
                text-align: center;
                color:#333;
              }
              span:nth-of-type(2){
                width:70%;
                color:#333;
              }
            }

          }
        }
        img {
          width:100%;
          transition: .5s;
          &:hover {
            transform: scale(1.02);
            z-index:99;
          }
        }
        .small-tip {
          display:inline-block;
          position:absolute;
          width:20px;
          height:20px;
          right:10%;
          bottom:50%;
          border-radius:50%;
          cursor: help;
          i {
            position:absolute;
            border-radius: 50%;
            width:16px;
            height:16px;
            margin-top:-8px;
            margin-left:-8px;
            left:50%;
            top:50%;
            transform-origin: center center;
            border:1px solid #000;
            /*transform: scale(0.5);*/
          }
          i:nth-of-type(1) {
            animation:big 2.4s linear 1.6s infinite;
          }
          i:nth-of-type(2) {
            animation:big 2.4s linear .8s infinite;
          }
          i:nth-of-type(3) {
            animation:big 2.4s linear infinite;
          }
        }
      }
    }
  }
}
</style>



